@mixin rwd($w) {
  @media only screen and (max-width: $w) {
    @content; } }
@mixin minW($w) {
  @media only screen and (min-width: $w) {
    @content; } }
@mixin rwdh($h:900px) {
  @media only screen and (max-height: $h) {
    @content; } }
@mixin minH($h:900px) {
  @media only screen and (min-height: $h) {
    @content; } }


@mixin clearfix {
  &:after {
    content: '';
    display: block;
    clear: both; } }

@mixin keyframes($name) {
	@-webkit-keyframes #{$name} {
		@content; }
	@-moz-keyframes #{$name} {
		@content; }
	@-ms-keyframes #{$name} {
		@content; }
	@keyframes #{$name} {
		@content; } }
@mixin animation($content) {
	-webkit-animation: $content;
	-moz-animation: $content;
	-o-animation: $content;
	animation: $content; }

@mixin before($txt:'') {
  &:before {
    content: $txt;
    display: block;
    @content; } }

@mixin after($txt:'') {
  &:after {
    content: $txt;
    display: block;
    @content; } }

@mixin hover($w:768px) {
  @media only screen and (min-width: $w) {
    &:hover {
      @content; } } }


@mixin pstc0($t:0,$l:0,$b:0,$r:0) {
  position: absolute;
  top: $t;
  bottom: $b;
  left: $l;
  right: $r;
  margin: auto; }

@mixin pstc5($t:50%,$l:50%) {
  position: absolute;
  top: $t;
  left: $l;
  transform: translate(-50%,-50%); }

@mixin fontH1() {
  font-size: 1.5rem;
  line-height: 1.5; }

@mixin fontH2() {
  font-size: 1.333333333333333rem;
  line-height: 1.5; }

@mixin fontH3() {
  font-size: 1.166666666666667rem;
  line-height: 1.5; }

@mixin fontP() {
  font-size: 1rem;
  line-height: 1.5; }

@mixin dib {
  display: inline-block;
  vertical-align: middle; }

* {
  padding: 0;
  margin: 0px;
  // outline: 1px solid #f00
  font-family: 'Roboto','Noto Sans TC', sans-serif; }

img,iframe {
  width: 100%;
  vertical-align: top; }

html {
  font-size: 15.5625px;
  @include rwd(415px) {
    font-size: 3.75vw; } }

body {
  background-image: url('../images/bg.png');
 }  // background-color: #f3c100

article {
  max-width: 415px;
  padding-bottom: 10%;
  margin: auto;
  overflow: hidden;
  position: relative;
  display: none;
  @include rwd(768px) {
    padding-bottom: 20%; } }

header {
  width: 100%;
  max-width: 415px;
  margin: auto;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 101;
  transition: .5s;
  &.active {
    background-color: #F1DA7B; }
  figure.logo {
    width: 10%;
    padding: .5rem 1rem; }
  .ham {
    width: 1.5rem;
    height: 1.5rem;
    position: absolute;
    top: 50%;
    right: 1rem;
    transform: translateY(-50%);
    z-index: 2;
    .line {
      width: 100%;
      height: 10%;
      background-color: #000;
      position: absolute;
      transition: .2s;
      &:nth-child(1) {
        top: 5%; }
      &:nth-child(2) {
        top: calc(50% - 5%); }
      &:nth-child(3) {
        top: calc(100% - 15%); } }
    &.active {
      .line {
        top: calc(50% - 5%); } }
    &.rotate {
      .line {
        &:nth-child(1) {
          transform: rotate(45deg); }
        &:nth-child(2) {
          transform: rotate(45deg); }
        &:nth-child(3) {
          transform: rotate(-45deg); } } } }
  section.menu {
    width: 100%;
    max-width: 415px;
    height: 100vh;
    background-color: #f7c700;
    position: fixed;
    top: -200vh;
    z-index: 1;
    transition: .5s;
    overflow-y: auto;
    &.active {
      top: 0; }
    .menu-box {
      width: 100%;
      padding: 10% 13.28125%;
      box-sizing: border-box;
      ul {
        list-style: none;
        li {
          cursor: pointer;
          figure.icon {
            width: 2.34375%;
            line-height: 1;
            font-size: 0;
            @include dib; }
          p {
            @include fontH1;
            @include dib;
            font-weight: bold;
            line-height: 5.25rem; }
          & + li {
            border-top: 1px solid #000; } } } } } }

footer {
  width: 100%;
  // padding: .5% 0
  // max-width: 415px
  margin: auto;
  background-color: #000;
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 201;
  text-align: center;
  p {
    color: #fffd64;
    font-size: 4vw;
    letter-spacing: 2vw;
    @include dib; }
  figure {
    width: 4.5vw;
    margin: 0 1%;
    @include dib; }
  // +rwd(1280px)
  //   p
  //     font-size: 3vw
  //   figure
  //     width: 7vw
  @include rwd(768px) {
    p {
      font-size: 5vw;
      letter-spacing: 1vw; }
    figure {
      width: 5.5vw; } }
  @include rwd(415px) {
    padding: 3.5% 0;
    p {
      font-size: 6vw;
      letter-spacing: .2vw; }
    figure {
      width: 7vw;
      margin: 0 0.5%; } } }

$red: #cc0000;
$yellow: #ffff00;



